@use '../../styles/utils/variables' as *;
@use '../../styles/utils/mixins' as *;

.calendar-day {
  display: flex;
  flex-direction: column;
  border: 1px solid $gray-200;
  border-radius: $border-radius-md;
  overflow: hidden;
  background: $white;
  min-height: 400px;
  @include transition(box-shadow);

  &:hover {
    box-shadow: $shadow-sm;
  }

  &--today {
    border-color: $primary-color;
    border-width: 2px;

    .calendar-day__header {
      background: linear-gradient(135deg, $primary-color 0%, darken($primary-color, 10%) 100%);
      color: $white;
    }
  }

  &--non-workday {
    background-color: $gray-100;
    opacity: 0.6;
  }

  &__header {
    background: $gray-100;
    padding: $spacing-sm;
    text-align: center;
    border-bottom: 2px solid $gray-200;
    @include flex-center;
    flex-direction: column;
    gap: $spacing-xs / 2;
  }

  &__weekday {
    font-size: $font-size-sm;
    font-weight: $font-weight-semibold;
    text-transform: uppercase;
    letter-spacing: 0.5px;
  }

  &__date {
    font-size: $font-size-xs;
    color: $gray-600;

    .calendar-day--today & {
      color: $white;
      opacity: 0.9;
    }
  }

  &__hours {
    flex: 1;
    display: flex;
    flex-direction: column;
    position: relative;
  }

  &__hour-row {
    display: grid;
    grid-template-columns: 60px 1fr;
    border-bottom: 1px solid $gray-200;
    min-height: 60px;

    &:last-child {
      border-bottom: none;
    }
  }

  &__hour-label {
    padding: $spacing-xs;
    font-size: $font-size-xs;
    color: $gray-600;
    text-align: right;
    padding-right: $spacing-sm;
    border-right: 1px solid $gray-200;
    background: grey;
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    padding-top: $spacing-xs;
  }

  &__hour-slot {
    position: relative;
    background: $white;
  }

  &__appointments {
    position: absolute;
    top: 0;
    left: 60px; // Width of hour labels
    right: 0;
    bottom: 0;
    pointer-events: none; // Allow clicks to pass through to slots

    > * {
      pointer-events: auto; // But enable clicks on appointment cards
    }
  }

  &__non-workday-label {
    @include flex-center;
    flex: 1;
    color: $gray-500;
    font-style: italic;
    font-size: $font-size-sm;
    padding: $spacing-lg;
  }

  &__empty {
    @include flex-center;
    flex: 1;
    color: $gray-400;
    font-style: italic;
    font-size: $font-size-sm;
    padding: $spacing-lg;
  }

  // Responsive adjustments
  @media (max-width: $breakpoint-md) {
    min-height: 300px;

    &__hour-row {
      min-height: 50px;
    }

    &__hour-label {
      grid-template-columns: 50px 1fr;
      font-size: $font-size-xs - 0.1rem;
    }

    &__appointments {
      left: 50px;
    }
  }
}
